<script>
import productService from "../services/ProductService.js";

export default {
  name: "Products",
  data() {
    return {
      filter: {
        PartNumber: "",
        InsideDiameter: null,
        OutsideDiameter: null,
        Width: null,
        Enclosure: "",
        ProductId: null,
        PriceCategory: "",
        Stock: null,
        Price: null,
      },
    };
  },

  methods: {
    getProducts() {
      productService.getAllProducts().then((response) => {
        this.$store.commit("SET_PRODUCTS", response.data);
      });
    },
  },

  created() {
    this.getProducts();
  },

  computed: {
    filteredProducts() {
      let filteredProducts = this.$store.state.products;
      if (this.filter.Enclosure == "sealed") {
        filteredProducts = filteredProducts.filter((product) =>
          product.Enclosure.includes(this.filter.Enclosure)
        );
      }
      return filteredProducts;
    },
  },
};
</script>

